import { useContext, useState, useEffect, Fragment} from "react";
import { useParams} from "react-router-dom";
import { GlobalState } from "../../../GlobalState";
import BirdItem from "../Utils/BirdItem/BirdItem";
import { Carousel } from "react-responsive-carousel";
import "react-responsive-carousel/lib/styles/carousel.min.css";

export default function DetailProduct() {
  const params = useParams();
  const state = useContext(GlobalState);
  const [birds] = state.birdsAPI.birds;
  const [detailBird, setDetailBird] = useState([]);



  useEffect(() => {
   
    if (params.id) {
        birds.forEach(bird => {
        if (bird._id === params.id) setDetailBird(bird);
      });
     
    } 
   
  }, [params.id, birds]);


  if (detailBird.length === 0) return null;

  return (
    
    <Fragment>

       <Carousel autoPlay={true} showArrows={false}>
         {detailBird.images.map((imagen,i) =>(
           <div key={i} ><img src={imagen} alt="" />
           
             </div>
         ))}
      </Carousel>



      <div className="detail">
        <div className="box-detail">

          <div className="row">
            <h2>"{detailBird.title}"</h2>
          </div>

          <span className="title-description"> <div className="title">Nombre</div>{detailBird.name}</span>
          <span className="title-description"> <div className="title">Nombre Local</div>{detailBird.localname}</span>
          <span className="title-description"><div className="title">Medidas</div>{detailBird.measure}</span><br/>
          <span className="title-description"><div className="title">Canto</div>{detailBird.song}</span><br/>
          <span className="title-description"><div className="title">Observacin</div>{detailBird.observation}</span><br/>
          {/* <span><div className="title">Ubicacin</div>{`Latitud: ${detailBird.lat} - Longitud: ${detailBird.lng}`}</span> */}
          <div className="title">Descripcin</div> <p className="title-description">{detailBird.description}</p>

        </div>
      </div>

      <div>
        <h2>Especies Relacionadas</h2>
        <div className="birds">
          {birds.map(bird => {
            return bird.category === detailBird.category ? (
              <BirdItem key={bird._id} bird={bird} />
            ) : null;
          })}
        </div>
      </div>
    </Fragment>
  );
}
