import { useContext, Fragment,useState} from "react";
import { GlobalState } from "../../../GlobalState";
import BirdItem from "../Utils/BirdItem/BirdItem";
import Loading from "../Utils/Loading/Loading";
import toast, { Toaster } from 'react-hot-toast';
import axios from 'axios';
import Filters from "./Filters";
import LoadMore from "./LoadMore";

export default function Birds() {
  const state = useContext(GlobalState);
  const [birds,setBirds] = state.birdsAPI.birds;
  const [isLogged] = state.userAPI.isLogged;
  const [token]= state.token
  const [callback,setCallback]=state.birdsAPI.callback 
  const [loading,setLoading]= useState(false)
  const [isChecked,setIsChecked]=useState(false)

  const deleteBird = async (id)=> {

    try {
      setLoading(true)
    
      const deleteBird= await axios.delete(`/api/birds/${id}`,{headers:{Authorization: token }})  
      await deleteBird
      setCallback(!callback)
      setLoading(false)
      toast.success(deleteBird.data.message)
    } catch (error) {
      toast.error(error.response.data.message)
    }
}


const handleCheck = (id)=> {
  // console.log(id)
  birds.forEach(bird => {
    if(bird._id === id) bird.checked = !bird.checked
})
setBirds([...birds])
}


const checkAll = ()=>{
  birds.forEach(bird => { 
    bird.checked =!isChecked
  })
  setBirds([...birds])
  setIsChecked(!isChecked)
}


const deleteAll =()=>{
  birds.forEach(bird => {
    if(bird.checked) deleteBird(bird._id)
  })
}



 if (loading) return <div ><Loading/></div>  // *Retorna el Loading

  return (
    <Fragment>
      <Filters/>
      {
        isLogged && 
        <div className="delete-all">
          <span>Seleccionar Todo</span>
          <input type="checkbox" checked={isChecked} onChange={checkAll}/>
          <button onClick={deleteAll}>Eliminar Todo</button>
        </div>
      }
      <div className="products">
        {birds.map((bird) => {
          return (
            
            <BirdItem
              key={bird._id}
              bird={bird}
              isLogged={isLogged}
              handleCheck={handleCheck}
              deleteBird={deleteBird}
            />
          );
        
        })}
      </div>
      <LoadMore/> 
      {birds.length === 0 && <Loading />}
      <Toaster  position="top-right"  reverseOrder={false}/>   {/* // * Componente de Notificacion */}
    </Fragment>
  );
}
