import React, { useState, useContext } from "react";
import axios from "axios";
import { GlobalState } from "../../../GlobalState";
import toast, { Toaster } from "react-hot-toast";

export default function Category() {
  const state = useContext(GlobalState);
  const initialState = { name: ""};
  const [categories] = state.CategoriesAPI.categories;
  const [callback, setCallback] = state.CategoriesAPI.callback;
  const [token] = state.token;
  const [onEdit, setOnEdit] = useState(false);
  const [id, setId] = useState("");
  const [category, setCategory] = useState(initialState);

  const onChangeInput = (e) => {
    const { name, value } = e.target;
    setCategory({ ...category, [name]: value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      if (onEdit) {
        const res = await axios.put(`/api/category/${id}`, { ...category }, { headers: { Authorization: token } });
        toast.success(res.data.message);
      } else {
        const res = await axios.post("/api/category", { ...category }, { headers: { Authorization: token } });
        toast.success(res.data.message);
      }
      setOnEdit(false);
      setCategory(initialState);
      setCallback(!callback);
    } catch (error) {
      toast.error(error.response.data.message);
    }

  };

  const editCategory = async (id, category) => {
    setId(id);
    setOnEdit(true);
    setCategory({ ...category});
  };
  
  const deleteCategory = async (id) => {
    try {
      const res = await axios.delete(`/api/category/${id}`, {
        headers: { Authorization: token },
      });
      setCallback(!callback);
      toast.success(res.data.message);
    } catch (error) {
      //  alert(error.response.data.message);
      toast.error(error.response.data.message);
    }
  };

  return (
    <>
      {/* FORMULARIO DE REGISTRO DE UN NUEVA CATEGORIA */}
      <div className="register-page">
        <form onSubmit={handleSubmit}>
          <h2>{onEdit ? "Actualizar Especie" : "Registrar Una Nueva Especie"}</h2>
          
          <input
            type="text"
            name="name"
            required
            placeholder="Nombre de especie"
            value={category.name}
            onChange={onChangeInput}
          />

          <div className="row">
            <button type="submit">{onEdit ? "Actualizar" : "Registrar"} </button>
          </div>
        </form>
        <Toaster position="top-right" reverseOrder={false} /> {/* // * Componente de Notificacion */}
      </div>

      {/*  TABLA DE USUARIOS */}
      <div className="history-page">
        <h2>Especies</h2>
        <table>
          <thead>
            <tr>
              <th>Nombre</th>
              <th>Acciones</th>
            </tr>
          </thead>
          <tbody>
            {categories.map((category) => (
              <tr key={category._id}>
                <td>{category.name}</td>
                <td>
                  <button className="btnedit" onClick={() => editCategory(category._id, category)}>
                    {" "}
                    Edit{" "}
                  </button>
                    <button
                      className="btndelete"
                      onClick={() => {
                        deleteCategory(category._id);
                      }}
                    >
                      Delete
                    </button>

                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </>
  );
}
