import { useState, useEffect } from "react";
import axios from "axios";

export default function Birdsapi() {
  const [birds, setBirds] = useState([]);
  const [callback, setCallback] = useState(false);
  const [category, setCategory] = useState("");
  const [sort, setSort] = useState("");
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [result, setResult] = useState(0);


  useEffect(() => {
    const getProducts = async () => {
   
      // * console.log('category effect',category)     
      const res = await axios.get(`/api/birds?limit=${page*9}&${category}&${sort}&title[regex]=${search}`);
      //* console.log("res.data",res.data.birds)
      setBirds(res.data.birds);
      setResult(res.data.result)
      
    };
    getProducts();
  }, [callback,category,sort,search,page]);

  return {
    birds: [birds, setBirds],
    callback: [callback, setCallback],
    category: [category, setCategory],
    sort: [sort, setSort],
    search: [search, setSearch],
    page: [page, setPage],
    result: [result, setResult]
  };
}
