import React, {useEffect,useState,useRef} from 'react'
import BtnRender from './BtnRender'
import { motion } from 'framer-motion'

export default function BirdItem({bird,isLogged,deleteBird,handleCheck}) {

const [width,setWidth] = useState(0)
const carousel = useRef()

useEffect(() => {
  setWidth(carousel.current.scrollWidth - carousel.current.offsetWidth);
  // console.log("carouselDentro",carousel)
},[])

  return (
    
    <div className="product_card">
      {
        isLogged && <input type="checkbox" checked={bird.checked} onChange={()=>handleCheck(bird._id)}/>
      } 
     {/* Carrusel de Imagenes */}
      <motion.div ref={carousel} className='slider-container'>
        <motion.div className='slider' drag='x' dragConstraints={{ right:0,left:-width }} >
        { bird.images.map((image,i) => (
            <motion.div className='item' key={i}>
                <img  src={image} alt="" />
            </motion.div>
        ))}
        </motion.div>
    </motion.div>
  
      <div className="product_box"> 
       <h2 className='titulo' title={bird.localname}>{bird.localname}</h2>
       {/* <span>{bird.category}</span> */}
       <p className='parrafo'>{bird.description}</p>
       </div>
      <BtnRender bird={bird} deleteBird={deleteBird}/>

    </div>
  )
}

