import { useState, useEffect } from "react";
import axios from "axios";

export default function Categoriesapi() {
  const [categories, setCategories] = useState([]);
  const [callback, setCallback] = useState(false);
  const [sort, setSort] = useState("");
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [result, setResult] = useState(0);


  useEffect(() => {
    const getCategories = async () => {
   
      // * console.log('category effect',category)     
      const res = await axios.get(`/api/category?limit=${page*9}&${sort}&title[regex]=${search}`);
      //* console.log("res.data",res.data.birds)
      setCategories(res.data.categories);
      setResult(res.data.result)
      
    };
    getCategories();
  }, [callback,sort,search,page]);

  return {
    categories: [categories, setCategories],
    callback: [callback, setCallback],
    sort: [sort, setSort],
    search: [search, setSearch],
    page: [page, setPage],
    result: [result, setResult]
  };
}
