const initialState = {
  title: "",
  name: "",
  localname: "",
  measure: "",
  song: "",
  observation: "",
  description: "",
  category: "",
  images: [],
  lat: "",
  lng: "",
  zoom: "",
  _id: "",
};

export default function CreateBird() {
  //const [zoom, setZoom] = useState(12); // initial zoom
  //const [center, setCenter] = useState({ lat: 0.12222, lng: -79.25380 });
  //const [valor, setValor] = useState({ lat: "", lng: "" });
  //const [clicks, setClicks] = useState([]);

  // const onClick = (e) => {
  //   setClicks([e.latLng]);
  //   setValor(e.latLng.toJSON());
  // };

  // const onIdle = (m) => {
  //   setZoom(m.getZoom());
  //   setCenter(m.getCenter().toJSON());
  // };

  const state = useContext(GlobalState);
  const [bird, setBird] = useState(initialState);
  const [categories] = state.CategoriesAPI.categories;
  const [file, setFile] = useState(null);
  const [loading, setLoading] = useState(false);
  const [token] = state.token;
  const navigate = useNavigate();
  const param = useParams();
  const [products] = state.birdsAPI.birds;
  const [onEdit, setOnEdit] = useState(false);
  const [check, setCheck] = useState(false);
  //const [checkmap, setCheckmap] = useState(false);
  const [callback, setCallback] = state.birdsAPI.callback;

  useEffect(() => {
    if (param.id) {
      setOnEdit(true);

      products.forEach((bird) => {
        if (bird._id === param.id) {
          setBird(bird);
          setFile(null);
        }
      }
      );
     
    } else {
      setOnEdit(false);
      setBird(initialState);
    }
  }, [param.id, products]);

  const handleChangeInput = (e) => {
    const { name, value } = e.target;
    setBird({ ...bird, [name]: value });
  };

  const handleCheck = () => {
    setCheck(!check);
    
  };

  // const handleCheckmap = () => {
  //   setCheckmap(!checkmap);
  // };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      if (onEdit) {
        if (file) {
          const formData = new FormData();
          for (let i = 0; i < file.length; i++) {
            formData.append("file", file[i]);
          }
          formData.append("title", bird.title);
          formData.append("name", bird.name);
          formData.append("localname", bird.localname);
          formData.append("measure", bird.measure);
          formData.append("song", bird.song);
          formData.append("observation", bird.observation);
          formData.append("description", bird.description);
          formData.append("category", bird.category);
          //checkmap ? formData.append("lat", valor.lat) : formData.append("lat", bird.lat);
          //checkmap ? formData.append("lng", valor.lng) : formData.append("lng", bird.lng);
          //formData.append("zoom", zoom);
          setLoading(true);
          const res = await axios.put(`/api/birds/${bird._id}`, formData, {
            headers: { "content-type": "multipart/form-data", Authorization: token },
          });
          toast.success(res.data.message);
        } else {
          setLoading(true);
       
          const res = await axios.put(
            `/api/birds/${bird._id}`,
            { ...bird},
            //{ ...bird, lat: checkmap ? valor.lat:bird.lat, lng: checkmap ? valor.lng:bird.lng, zoom },
            { headers: { Authorization: token } }
          );
          toast.success(res.data.message);
        }
      } else {
        // if (clicks.length > 0) {
          if (file) {
            const formData = new FormData();
            for (let i = 0; i < file.length; i++) {
              formData.append("file", file[i]);
            }
            formData.append("title", bird.title);
            formData.append("name", bird.name);
            formData.append("localname", bird.localname);
            formData.append("measure", bird.measure);
            formData.append("song", bird.song);
            formData.append("observation", bird.observation);
            formData.append("description", bird.description);
            formData.append("category", bird.category);
            // formData.append("lat", valor.lat);
            // formData.append("lng", valor.lng);
            // formData.append("zoom", zoom);
            setLoading(true);
            const res = await axios.post("/api/birds", formData, {
              headers: { "content-type": "multipart/form-data", Authorization: token },
            });
            setLoading(false);
            toast.success(res.data.message);
          } else {
            return toast.error("Debe cargar una o varias imgenes");
          }
        // } else {
        //   return toast.error("Seleccionar una ubicacin en el mapa");
        // }
      }
      setCallback(!callback);
   
      setBird(initialState);
      navigate("/"); // * navegamos a inicio
    } catch (err) {
      toast.error(err.response.data.message);
    }
  };

  return (
    <div className="create_product">
      {/* {(!onEdit || checkmap) && (
        <div className="upload">
          <Wrapper apiKey={"AIzaSyCWFupoeZD2JJMcPcrev5T5MmxVsSYB2wA"} render={render}>
            <Map center={center} onIdle={onIdle} onClick={onClick} style={{ width: "100%", height: "100%" }} zoom={zoom}>
              {clicks.map((latLng, i) => (
                <Marker key={i} position={latLng} />
              ))}
            </Map>
          </Wrapper>
        </div>
      )} */}

      <form onSubmit={handleSubmit}>
        {/* MAPA INPUTS  */}
        {/* <input type="hidden" id="zoom" name="zoom" value={zoom} disabled onChange={(event) => setZoom(Number(event.target.value))} /> */}
        {/* <br /> */}
        {/* {onEdit && !checkmap ? (
          <div className="rowchecked">
            <input type="checkbox" name="my-checkbox" id="opt-in" checked={checkmap || false} onChange={handleCheckmap} />
            <label htmlFor="opt-in">Cargar nuevas coordenadas</label>
          </div>
        ) : (
          ""
        )} */}

        {/* {checkmap || clicks.length > 0 ? (
          <>
            <div className="row">
              <label htmlFor="lat">Latitud</label>
              <input
                type="number"
                id="lat"
                name="lat"
                value={valor.lat}
                disabled
                onChange={(event) => setValor({ lat: Number(event.target.value) })}
              />
            </div>
            <div className="row">
              <label htmlFor="lng">Longitud</label>
              <input
                type="number"
                id="lng"
                name="lng"
                value={valor.lng}
                disabled
                onChange={(event) => setValor({ lng: Number(event.target.value) })}
              />
            </div>
          </>
        ) : (
          ""
        )} */}

        {onEdit && (
          <div className="rowchecked">
            <input type="checkbox" name="my-checkbox" id="opt-in" checked={check || false} onChange={handleCheck} />
            <label htmlFor="opt-in">Cargar nuevas fotos</label>
          </div>
        )}

        {(!onEdit || check) && (
          <div className="row">
            <label htmlFor="fichero" className="circle">
              {" "}
              <BsImages style={{ fontSize: "45px" }} />
              Cargar Imgenes
            </label>
            <input id="fichero" type="file" onChange={(e) => setFile(e.target.files)} accept="image/*" multiple />
          </div>
        )}


        <div className="row">
          <label htmlFor="title">Nombre Cientfico</label>
          <input
            type="text"
            name="title"
            placeholder="Ingrese nombre cientfico del ave"
            id="title"
            required
            value={bird.title}
            onChange={handleChangeInput}
          />
        </div>
        <div className="row">
          <label htmlFor="name">Nombre</label>
          <input
            type="text"
            name="name"
            placeholder="Ingrese nombre del ave"
            id="name"
            required
            value={bird.name}
            onChange={handleChangeInput}
          />
        </div>
        <div className="row">
          <label htmlFor="localname">Nombre Local</label>
          <input
            type="text"
            name="localname"
            placeholder="Ingrese nombre local del ave"
            id="localname"
            required
            value={bird.localname}
            onChange={handleChangeInput}
          />
        </div>
        <div className="row">
          <label htmlFor="measure">Medidas</label>
          <input
            type="text"
            name="measure"
            placeholder="Ingrese la medida del ave"
            id="measure"
            required
            value={bird.measure}
            onChange={handleChangeInput}
          />
        </div>
        <div className="row">
          <label htmlFor="song">Canto</label>
          <input
            type="text"
            name="song"
            placeholder="Ingrese el canto del ave"
            id="song"
            required
            value={bird.song}
            onChange={handleChangeInput}
          />
        </div>
        <div className="row">
          <label htmlFor="observation">Observacin</label>
          <input
            type="text"
            name="observation"
            placeholder="Ingrese alguna observacin"
            id="observation"
            required
            value={bird.observation}
            onChange={handleChangeInput}
          />
        </div>

        <div className="row">
          <label htmlFor="description">Descripcin</label>
          <textarea
            type="text"
            name="description"
            id="description"
            placeholder="Escriba una descripcin......."
            required
            value={bird.description}
            rows="5"
            onChange={handleChangeInput}
          />
        </div>

        <div className="row">
          <label htmlFor="categories">Especies: </label>
          <select name="category" value={bird.category} onChange={handleChangeInput} required>
            <option value="">Seleccione una Especie</option>
            {categories.map((category) => (
              <option value={category.name} key={category._id}>
                {category.name}
              </option>
            ))}
          </select>
        </div>
        {loading ? <Loading /> : <button type="submit">{onEdit ? "Actualizar" : "Registrar"}</button>}
      </form>
      <Toaster position="top-right" reverseOrder={false} />
    </div>
  );
}
