import {useContext} from 'react'
import { Link } from "react-router-dom";
import {GlobalState} from "../../../../GlobalState"


export default function BtnRender({bird,deleteBird}) {

  const state = useContext(GlobalState);
  const [isLogged]=state.userAPI.isLogged


  return (
     <div className="row_btn">
       {
         isLogged ? 
         <>
         <Link id="btn_delete" to="#!" onClick={()=>deleteBird(bird._id)}>Eliminar</Link>
         <Link id="btn_view_log" to={`/edit_product/${bird._id}`}>Editar</Link>
         </>
         :
         <>
         <Link id="btn_view" to={`/detailbird/${bird._id}`}>Detalles</Link>
         </>
         
       }
         
      </div> 
  )
}

